<template>
  <div class="container"> 
    <div class="loginMoudel" ref="loginMoudel">
      <el-form ref="ruleForm" :rules="rules" :model="ruleForm" label-width="80px">
        <div class="loginForm">
            <p class="title mt25">甘肃省环保专业大学生</p>
            <p class="title mb25">就业创业平台</p>
            <el-form-item  class="phone" prop="username">
              <span>+86</span><el-input v-model="ruleForm.username" placeholder="请输入手机号码"></el-input>
            </el-form-item >
            <el-form-item  class="pwd"  prop="password">
              <el-input v-model="ruleForm.password" type="password" placeholder="请输入账号密码"></el-input>
            </el-form-item >
            <el-form-item class="codeModel"  prop="code">
              <div class="code">
                <el-input v-model="ruleForm.code" placeholder="验证码"></el-input>
              </div>
              <div class="codeCanvas" @click="changeCode">
                <img :src="codeSrc" alt="点击刷新"/>
                <!-- <s-identify :identifyCode="identifyCode" ></s-identify> -->
              </div>
            </el-form-item>
            <div class="loginBtn">
              <button type="button"  @click="submitForm('ruleForm')">登录创业平台</button>  
            </div>
              <el-form-item class="agree" prop="checked">
                <el-checkbox  label="我已同意用户协议及隐私政策" name="type" v-model="ruleForm.checked"/>
                <span class="regist"><router-link to="/Regist">免费注册</router-link></span>
              </el-form-item>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import {captchaImage,login} from '@/api/loginReg'
export default {
  name:'login',
  data(){
  // 勾选协议
    const validateVerifycode = (rule, value, callback) => {
      console.log(value)
      if (value === false) {
        callback(new Error('请勾选用户协议及隐私政策'))
      } else {
        callback()
      }
    }
    return{
      codeSrc:'',
      ruleForm:{
        username:'',
        password:'',
        code:'',
        uuid:'',
        checked:false
      },
      rules:{
        username:[
            { required: true, message: '手机号不能为空', trigger: 'blur' }
        ],
        password:[
            { required: true, message: '密码不能为空', trigger: 'blur' }
        ],
        code:[
            { required: true, message: '请输入验证码', trigger: 'blur' }
        ],
        checked:[
            { required: true, trigger: 'change',validator: validateVerifycode }
        ]
      }
    }
  },
  mounted() {
     this.resiZie()
     window.onresize = () => {
            return (() => {
              this.resiZie()
            })()
        }
        this.changeCode();
  },
  methods: {
    resiZie(){
      const headerHeight = this.$root.$children[0].$children[0].$refs.header.$el.offsetHeight;
      const fooer = this.$root.$children[0].$children[0].$refs.footer.$el.offsetHeight;
      this.$refs.loginMoudel.style.height =(window.innerHeight - headerHeight - fooer) +'px';
    },   
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {

        if (valid) {
          login(this.ruleForm).then(res=>{
            if(res.code === 200) {
                localStorage.setItem('token',res.token);
                this.$router.push({ name: 'HomePage'});
            }else {
              this.$message.error(res.msg);
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    changeCode(){
      captchaImage().then(res => {
        if(res.code === 200){
          this.codeSrc = 'data:image/jpeg;base64,'+res.img;
          this.ruleForm.uuid = res.uuid;
        }else{
            this.$message.error(res.msg);
        }
      })
    }
  },
}
</script>
<style lang="less" >
  .loginMoudel{
    width: 100%;
    position: relative;
    background: url('../assets/img/loginBg.jpg') repeat-y;
    background-size: 100% 100%;
    .loginForm{
      width: 347px;
      // height: 336px;
      position: absolute;
      right: 40px;
       top: 50%;
       margin-top: -190.5px;
      // margin: auto;
      padding-bottom: 15px;
      background-color: #fff;
      border-radius: 5px;    
      font-size: 13px;
      letter-spacing: normal;
      color: #333333;
      .mt25{
        font-size: 22px;
        color: #0c8888;
        margin-top: 25px;
      }
      .mb25{
        font-size: 22px;
        color: #0c8888;
        margin-bottom: 25px;
      }
      .phone{
        .el-form-item__content{
          margin-left:0!important;
          text-align: left;
        }
        background: url('../assets/img/phone.png') no-repeat;
        height: 40px;
        margin:0 40px 20px 40px;
        border:1px solid #10bbb8;
        background-size: 40px;
        border-radius: 5px;
        padding-left: 40px;
        text-align: left;
        span{
          display: inline-block;
          width: 20px;
          height: 40px;
          line-height: 40px;
          margin:0 10px;
          color: #858585;
        }
        .el-input{
          width:180px;
          line-height: 40px;
          color: #858585;
          .el-input__inner{
            border: 0 none;
            outline: 0 none;
            padding: 0;
            height:40px;
          }
        }
        .el-form-item__error{
          left: -40px;
        }
      }
      .pwd{
        .el-form-item__content{
          margin-left:0!important;
          text-align: left;
        }
        background: url('../assets/img/pwd.png') no-repeat 3px 5px;
        height: 40px;
        margin:0 40px 20px 40px;
        border:1px solid #10bbb8;
        background-size:  32px 33px;
        border-radius: 5px;
        padding-left: 40px;
        text-align: left;
        .el-input{
          margin-left: 10px;
          height:40px;
          line-height: 40px;
          color: #858585;
          .el-input__inner{
            border: 0 none;
            width:180px;
            outline: 0 none;
            padding: 0;
          }
        }
        .el-form-item__error{
          left: -40px;
        }
      }
      .loginBtn{
          margin: 10px 40px 0 40px;
        button{
          display: inline-block;
          background-color: #10bbb8;
          outline: none;
          color: #fff;
          width: 100%;
          padding: 10px 0;
          border: 0 none;
          border-radius: 3px;
          &:hover{
            cursor: pointer;
          }
        }
      }
      .agree{
        margin: 10px 40px 0 40px;
        text-align: left;
        .el-form-item__content{
          margin-left:0!important;
          text-align: left;
          font-size: 12px;
        line-height: 12px;
          .el-checkbox__label{
            font-size: 12px;
          }
        }
        .regist{
          float: right;
          a{
            color: #3F9DFF;
            &:hover{
            cursor: pointer;
            text-decoration: underline;
            }
          }
          line-height: 18px;
        }
      }
    }
  }
  .codeModel{
        margin:0 0px 20px 40px;
        .el-form-item__content{
          margin-left:0!important;
          text-align: left;
        }
        text-align: left;
        .codeCanvas{
          margin-left: 10px;
          display: inline-block;
          border: 1px solid #10bbb8;
          height:40px;
          line-height: 40px;
          vertical-align: middle;
          border-radius: 3px;
          width: 120px;
          img{
            width: 100%;
            height: 100%;
          }
        }
        .code{
            display: inline-block;
            background: url('../assets/img/code.png') no-repeat 3px 5px;
            height: 40px;
            border:1px solid #10bbb8;
            background-size:  32px 33px;
            border-radius: 5px;
            padding-left: 40px;
            width: 90px;
            text-align: left;
            input{
              margin-left: 10px;
              width:75px;
              height:40px;
              line-height: 40px;
              border: 0 none;
              outline: 0 none;
              padding: 0;
              color: #858585;
            }
          }
      }
</style>


