// 简历信息管理接口
import api from '../utils/api';

// 获取简历列表
export async function register(params) {
    let _str = '?'
    for(let i in params) {
        _str += i + '='+params[i] + '&'
    }
    const path = '/env/user/register'+_str;
    const data = await api.post(path);
    return data;
}
// 获取简历列表
export async function login(params) {
    let _str = '?'
    for(let i in params) {
        _str += i + '='+params[i] + '&'
    }
    const path = '/env/user/login'+_str;
    const data = await api.post(path,params);
    return data;
}
// 获取验证码
export async function captchaImage(params) {
    const path = '/captchaImage';
    const data = await api.get(path,{params});
    return data;
}



